.card-menus .accordion-section-title {
  border: 0 !important;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.card-menus .accordion-section-title::after {
  content: "" !important;
}
.card-menus .accordion-section-title .text {
  flex-basis: 80%;
}
.card-menus .accordion-section-title .icon {
  text-align: right;
  flex-basis: 20%;
}
.card-menus #post-body-content {
  min-width: auto !important;
}